.collabList {
  list-style: none;
}

.collabList li {
  color: #333;
  background-color: rgba(95, 197, 252, .5);
  padding-left: 10px;
  padding: 5px;
  margin-bottom: 10px;
  border-radius: 5px;
  -webkit-transition: background-color .2s ease-out;
  -o-transition: background-color .2s ease-out;
  transition: background-color .2s ease-out;
}

.collabList li:hover {
  background-color: pink;
  cursor: pointer;
}
ul.collabList {
  padding: 0;
}
.helperText {
  margin-bottom: -5px;
  position: relative;
  display: none;
}

input + .helperText {
  display: none
}

input:focus + .helperText {
  display: block
}



